@import "~normalize.css/normalize.css";

@font-face {
    font-family: 'graphik-regular';
    font-style: normal;
    font-weight: 500;
    src: url(../fonts/graphik/graphik-regular.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

@font-face {
    font-family: 'graphik-bold';
    src: url("../fonts/graphik/Graphik-Bold-Web.eot");
    src: url("../fonts/graphik/Graphik-Bold-Web.eot?#iefix") format("embedded-opentype"), url("../fonts/graphik/Graphik-Bold-Web.woff2") format("woff2"), url("../fonts/graphik/Graphik-Bold-Web.woff") format("woff"), url("../fonts/graphik/Graphik-Bold-Web.ttf") format("truetype");
    font-weight: 700;
    font-style: normal
}

@font-face {
    font-family: 'graphik-Italic';
    src: url("../fonts/graphik/Graphik-RegularItalic-Web.eot");
    src: url("../fonts/graphik/Graphik-RegularItalic-Web.eot?#iefix") format("embedded-opentype"), url("../fonts/graphik/Graphik-RegularItalic-Web.woff2") format("woff2"), url("../fonts/graphik/Graphik-RegularItalic-Web.woff") format("woff"), url("../fonts/graphik/Graphik-RegularItalic-Web.ttf") format("truetype");
    font-weight: normal;
    font-style: italic;
}

@font-face {
    font-family: 'graphik-black';
    src: url("../fonts/graphik/Graphik-Black-Web.eot");
    src: url("../fonts/graphik/Graphik-Black-Web.eot?#iefix") format('embedded-opentype'), url("../fonts/graphik/Graphik-Black-Web.woff2") format('woff2'), url("../fonts/graphik/Graphik-Black-Web.woff") format('woff'), url(/template/fonts/../fonts/graphik/Graphik-Black-Web.ttf) format('truetype'), url(/template/fonts/../fonts/graphik/Graphik-Black-Web.svg#../fonts/graphik/Graphik-Black-Web) format('svg');
    font-weight: 900;
    font-style: normal
}

body {
    color: #333;
    font-size: 16px;
    font-family: graphik-regular, helvetica, sans-serif;
    font-weight: 400;
    background: #ededed;
    height: 100%;
}

//common
ul {
    padding: 0;
    margin: 0;

    li {
        list-style: none;
    }
}

//button
.button {
    touch-action: manipulation;
    position: relative;
    display: inline-block;
    margin: 0;
    padding: 0;
    user-select: none;
    border: 0;
    outline: 0;
    text-align: center;
    white-space: nowrap;
    height: 48px;
    cursor: pointer;

    &__text {
        position: relative;
        display: inline-block;
        overflow: hidden;
        text-overflow: ellipsis;
        vertical-align: middle;
        padding: 0 32px;
    }

    &:after {
        content: '';
        display: inline-block;
        vertical-align: middle;
        height: 100%;
    }

    &_theme {
        &_green {
            border-radius: 100px;
            background-color: #00b956;

            .button__text {
                color: #ffffff;
                font-size: 15px;
                line-height: 24px;
            }

            &:hover {
                background: #33c778;
            }

            &:active {
                background: #0c9d47;
            }
        }
    }

    &_type {
        &_link {
            text-decoration: none;
        }
    }
}

//link
.link {
    text-decoration: none;
    color: #333;

    &_theme {
        &_default {
            color: #191919;
            font-size: 12px;
            font-weight: 400;
            line-height: 15px;
            display: inline-block;
        }
    }

    &_arrow {
        &_true {
            position: relative;

            &:hover {
                text-decoration: underline;
            }

            &:after {
                content: '';
                position: absolute;
                background-image: url(../img/arrow-link.png);
                width: 16px;
                height: 16px;
                margin-left: 10px;
            }
        }
    }
}

//topbar
.topbar {
    position: relative;
    width: 100%;
    font-size: 12px;
    background: #333333;

    &__wrap {
        display: flex;
        position: relative;
        max-width: 1440px;
        margin: 0 auto;
        padding: 0 40px;
        line-height: 30px;
        box-sizing: border-box;
        color: #FFFFFF;

        @media screen and (max-width: 1272px) {
            padding: 0 20px;
        }

        @media screen and (max-width: 767px) {
            line-height: 28px;
        }
    }
}

//menu
.menu {
    list-style: none;
    margin: 0;
    padding: 0;
    border: 0;
    margin: 0 -15px;

    @media screen and (max-width: 767px) {
        display: none;
    }

    &__item {
        position: relative;
        display: inline-block;
        height: 30px;
        width: auto;
        vertical-align: top;
        font-size: 13px;
        text-align: center;

        &-link {
            display: block;
            margin: 0;
            padding: 0 15px;
            text-decoration: none;
            color: #FFFFFF;
            font-size: 12px;

            &:hover {
                background: #00B956;
                color: #FFFFFF;
            }
        }
    }
}

//dropdown 
.dropdown {
    &__link {
        display: block;
        max-width: 120px;
        padding: 0 25px 0 20px;
        color: #FFFFFF;
        text-decoration: none;
        overflow: hidden;
        text-overflow: ellipsis;
        font-size: 12px;
        white-space: nowrap;
        cursor: pointer;
        user-select: none;
    }

    &__list {
        position: absolute;
        left: 0;
        width: 230px;
        background: #FFFFFF;
        border: 1px solid #EAECED;
        box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.15);
        border-top: 0;
        display: none;

        &-link {
            display: block;
            padding: 5px 15px;
            color: #333333;
            text-decoration: none;
            box-sizing: border-box;
        }
    }

    &_type {
        &_menu {
            display: none;
            position: relative;

            @media screen and (max-width: 767px) {
                display: block;

                .dropdown__link {
                    margin-left: -16px;
                }
            }
        }

        &_region {
            position: relative;
            margin-left: auto;


        }

    }

    &__link {
        position: relative;

        &:hover {
            color: #00B956;

            &:after {
                border-color: #00B956 transparent transparent !important;
            }
        }

        &:after {
            content: '';
            position: absolute;
            right: 8px;
            top: 50%;
            margin-top: -2px;
            width: 0;
            height: 0;
            border: 4px solid;
            border-color: #FFFFFF transparent transparent;
        }
    }
}

//header
.header {
    height: 52px;
    box-shadow: 0 2px 15px rgba(0, 0, 0, 0.1);
    background-color: #ffffff;

    &__logo {
        .mobile {
            display: none;
        }

        @media screen and (max-width: 767px) {
            .mobile {
                display: block;
            }

            .desktop {
                display: none;
            }
        }
    }

    &__wrap {
        height: 100%;
        margin: 0 auto;
        width: auto;
        max-width: 1440px;
        right: auto;
        left: auto;
        box-sizing: border-box;
        padding: 14px 80px;

        @media screen and (min-width: 1282px) {
            padding: 14px 40px
        }

        @media screen and (max-width: 1272px) {
            padding: 14px 20px;
        }

        .header__mobille-menu {
            display: none;
        }

        @media screen and (max-width: 767px) {
            .header__mobille-menu {
                display: flex;
                text-decoration: none;
                align-items: center;
                justify-content: space-between;
                width: 42%;
            }

            display: flex;
            align-items: center;
            justify-content: space-between;

            .header__mobille-menu__item {
                display: flex;
                text-decoration: none;

            }
        }
    }
}

//banner 
.banner {
    display: block;
    text-decoration: none;
    color: #000;

    &_type {
        &_main {
            color: #fff;
            height: 500px;
            background-size: cover;
            background-repeat: no-repeat;

            @media screen and (max-width: 767px) {
                height: 240px;
                background-position: 25% 50%;
            }
        }
    }

    &__wrap {
        width: 100%;
        height: 100%;
        max-width: 1440px;
        padding: 0 40px;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        margin: 0 auto;
        right: auto;
        left: auto;

        @media screen and (max-width: 1272px) {
            padding: 0 20px;
        }
    }

    &__title {
        font-size: 52px;
        font-weight: 700;
        line-height: 56px;
        color: #fff;
        letter-spacing: -1.7px;

        @media screen and (max-width: 1019px) {
            font-size: 42px;
        }

        @media screen and (max-width: 767px) {
            font-size: 28px;
            line-height: 34px;
            letter-spacing: normal
        }
    }

    &__content {
        max-width: 50%;

        @media screen and (max-width: 1272px) {
            max-width: 80%
        }

        @media screen and (max-width: 767px) {
            max-width: 100%
        }
    }

    &__subtitle {
        font-size: 14px;
        line-height: 24px;
        margin-top: 16px;

        @media screen and (max-width: 767px) {
            font-size: 15px;
        }
    }
}

//container

.container {
    margin: 0 auto;
    width: auto;
    max-width: 1440px;
    right: auto;
    left: auto;
    padding: 0 80px;
    box-sizing: border-box;

    @media screen and (min-width: 1282px) {
        padding: 0 40px;
    }

    @media screen and (max-width: 1272px) {
        padding: 0 20px;
    }
}

//info-block
.info-block {
    &_theme {
        &_while {
            background: #fff;
        }
    }

    &__card-list {
        margin-top: 30px;
        margin-bottom: 20px;
    }

    &_inner {
        margin-bottom: 80px;

        @media screen and (max-width: 767px) {
            margin-bottom: 20px;
        }
    }

    &__link {
        color: #191919;
        font-size: 12px;
        font-weight: 400;
        line-height: 15px;
        margin-top: 24px;
        display: inline-block;

        @media screen and (max-width: 767px) {
            margin-top: 8px;
        }
    }

    &__title {
        max-width: 380px;
    }

    &__subtitle {
        margin-top: 35px;

        @media screen and (max-width: 767px) {
            margin-top: 10px;
        }
    }

    &__image {

        &-wrap {
            height: 100%;
            background-position: 50%;
            background-repeat: no-repeat;
            background-size: cover;
            position: relative;

            @media screen and (max-width: 767px) {
                height: 240px;
                opacity: 0.8;

                .title.title_type_h2 {
                    max-width: 250px;
                }
            }


        }

        &-content {
            position: absolute;
            width: 100%;
            height: 100%;
            padding: 35px 48px;
            box-sizing: border-box;

            @media screen and (max-width: 1019px) {
                padding: 20px;
            }
        }
    }

    &_type {
        &_main {
            padding-top: 50px;

            @media screen and (max-width: 767px) {
                padding-top: 42px;
            }
        }

        &_info-banner {
            padding-bottom: 80px;
            padding-top: 80px;
            overflow: hidden;

            @media screen and (max-width: 767px) {
                padding-top: 50px;
            }

            .info-block__description {
                color: #333333;
                font-size: 15px;
                line-height: 20px;
                margin-top: 10px;
                max-width: 320px;

                @media screen and (max-width: 767px) {
                    max-width: 100%;
                    margin-top: 25px;
                    line-height: 24px;
                }
            }

            .title_type_h2 {
                margin-bottom: 0;
                max-width: 350px;
            }
        }

        &_сomments {
            padding-top: 62px;
            padding-bottom: 120px;

            @media screen and (max-width: 767px) {
                padding-top: 16px;
                padding-bottom: 0;
            }

            .info-block__name {
                max-width: 320px;

                @media screen and (max-width: 767px) {
                    max-width: 100%;
                }
            }
        }
    }

    &__block {
        background: #fff;
        height: 100%;
        padding: 44px 80px 30px 26px;
        padding-right: 80px;
        box-sizing: border-box;

        @media screen and (max-width: 1019px) {
            padding: 20px;
        }

        &-full {
            padding: 50px 40px 30px 40px;

            @media screen and (max-width: 767px) {
                margin: 0 -20px;
                margin-bottom: 50px;
                padding: 42px 20px;
                padding-left: 20px !important;
                padding-right: 20px !important;

                &:last-child {
                    margin-bottom: 0;
                }
            }

        }

        &-info {
            padding: 0 10px;

            @media screen and (max-width: 767px) {
                padding: 0;
            }

            .info-block__description {
                max-width: 100% !important;
            }
        }

        &-wrap {
            padding-bottom: 40px;

            @media screen and (max-width: 767px) {
                padding-bottom: 31px;
            }

            border-bottom: 1px solid #ebedef;
        }

        &-text {
            color: #333333;
            font-size: 15px;
            line-height: 20px;

            @media screen and (max-width: 767px) {
                margin-top: 27px;
                line-height: 24px;
            }

        }

        &-items {
            margin-top: 82px;

            @media screen and (max-width: 1019px) {

                margin-top: 50px;
            }

            @media screen and (max-width: 767px) {
                margin-top: 56px;

                .info-block__block-item_bullet {
                    border-bottom: 1px solid #ebedef;
                    margin-bottom: 28px;

                    &:last-child {
                        border-bottom: 0;
                        margin-bottom: 0;
                    }

                    .info-block__block-wrap {
                        border-bottom: 0
                    }
                }
            }

        }

        &-item {
            margin-bottom: 30px;

            @media screen and (max-width: 767px) {
                margin-bottom: 0;
            }

            &:last-child {
                .info-block__block-wrap {
                    border-bottom: 0;
                }
            }

            &_bullet {
                padding-left: 40px;
                position: relative;

                @media screen and (max-width: 767px) {
                    padding-left: 30px;
                }

                &::after {
                    content: '';
                    position: absolute;
                    left: 5px;
                    top: 11px;
                    width: 10px;
                    height: 10px;
                    background-color: #00b956;
                    border-radius: 50px;
                }
            }

            &_name {
                color: #191919;
                font-size: 20px;
                font-weight: 700;
                line-height: 28px;

                @media screen and (max-width: 767px) {
                    font-size: 18px;
                }
            }

            &_text {
                color: #333333;
                font-size: 15px;
                line-height: 24px;
                margin-top: 6px;
                padding-right: 60px;

                @media screen and (max-width: 767px) {
                    padding-right: 0;
                    margin-top: 12px;
                }
            }
        }

        &_theme {
            &_gray {
                padding: 0;
                background: #ededed;

                .info-block__name {
                    margin-top: 0;
                }

                .info-block__description {
                    max-width: 415px;
                    line-height: 23px;

                    @media screen and (max-width: 767px) {
                        max-width: 100%;
                        margin-top: 26px;
                        font-size: 15px;
                    }
                }
            }
        }
    }

    &__row {
        display: flex;
        flex-wrap: wrap;
    }

    &__col {
        max-width: 50%;
        width: 100%;

        @media screen and (max-width: 767px) {
            max-width: 100%;
        }

        &.colSize9 {
            width: 100%;
            max-width: 75%;

            @media screen and (max-width: 1019px) {
                max-width: 100%
            }
        }
    }

    &__description {
        color: #191919;
        font-size: 20px;
        font-weight: 500;
        letter-spacing: 0.5px;
        line-height: 28px;
        margin-top: 32px;
        max-width: 75%;

        @media screen and (max-width: 767px) {
            max-width: 100%;
            font-size: 18px;
            margin-top: 28px;
            line-height: 25px;

        }
    }

    &__name {
        max-width: 75%;

        @media screen and (max-width: 767px) {
            max-width: 100%;
        }
    }

    &__advantages {
        margin-top: 35px;

        &-item {
            display: flex;
            margin-bottom: 43px;

            @media screen and (max-width: 767px) {
                display: block;
            }

            &:last-child {
                .info-block__advantages-content {
                    border-bottom: 0;
                }
            }
        }

        &-content {
            margin-left: 40px;
            padding-bottom: 45px;
            max-width: 75%;
            border-bottom: 2px solid #ebedef;

            @media screen and (max-width: 767px) {
                margin-left: 0;
                max-width: 100%;
                margin-top: 30px;
                border-bottom: 0;
                padding-bottom: 5px;
            }
        }

        &-name {
            color: #191919;
            font-size: 20px;
            font-weight: 700;
            line-height: 28px;

            @media screen and (max-width: 767px) {
                font-size: 18px;
            }
        }

        &-text {
            color: #333333;
            font-size: 15px;
            font-weight: 400;
            line-height: 24px;
            margin-top: 5px;

            @media screen and (max-width: 767px) {
                margin-top: 10px;
            }
        }
    }
}

//title 
.title {
    &_type {
        &_h2 {
            font-size: 32px;
            font-weight: 600;
            letter-spacing: 0.5px;
            line-height: 40px;
            font-style: normal;
            font-family: 'graphik-bold';
            font-weight: 700;

            @media screen and (max-width: 767px) {
                font-size: 22px;
            }

            @media screen and (max-width: 767px) {
                line-height: 27px;
            }
        }

        &_h3 {
            color: #191919;
            font-size: 20px;
            letter-spacing: 0.63px;
            line-height: 24px;

            @media screen and (max-width: 767px) {
                font-size: 18px;
            }
        }
    }

    &_theme {
        &_while {
            color: #fff
        }
    }
}

//icon
.icon {
    width: 37px;
    height: 32px;
}

//footer
