    }

    &__wrap {
        display: block;
        position: relative;
        max-width: 1440px;
        margin: 0 auto;
        padding: 0 40px;
        line-height: 30px;
        box-sizing: border-box;

        @media screen and (max-width: 1272px) {
            padding: 0 20px;
        }
    }

    &__menu {
        display: inline-block;
        vertical-align: top;

        &_size {
            &_1 {
                width: 32%;

                @media screen and (max-width: 767px) {
                    width: 100%;
                }
            }

            &_2 {
                width: 68%;

                @media screen and (max-width: 767px) {
                    width: 100%;
                }

                .footer__list {
                    display: flex;
                    flex-wrap: wrap;
                }

                .footer__list-item {
                    width: 50%;
                }
