        }
    }

    &__col {
        max-width: 50%;
        width: 100%;
        display: flex;
        flex-wrap: wrap;

        @media screen and (max-width: 767px) {
            width: 100%;
            max-width: 100%
        }
    }

    &__head {
        font-size: 16px;
        font-weight: 400;
        line-height: 20px;
        margin-bottom: 10px;
        width: 100%;
        display: block;
        text-decoration: none;
        color: #333333;

        @media screen and (max-width: 767px) {}


    }

    a.footer__head {
        &:hover {
            text-decoration: underline;
        }
    }

    &__list {
        &_icon {
            .footer__list-item {
                display: flex;
                align-items: center;
                margin-bottom: 6px;

                svg {
                    width: 40px;
                    height: 26px;
                    margin-right: 4px;
                }
            }

        }

        &-link {
            font-size: 12px;
            font-weight: 400;
            line-height: 15px;
            color: #b1b6ba;

            &:hover {
