    }

    &__copy_inn {
        color: #b1b6ba;
        font-size: 12px;
        font-weight: 400;
        margin-left: 15px;
        line-height: 15px;

    }
}

//accordion
.accordion {
    &:first-child {
        border-top: 1px solid #ebedef;
    }

    border-bottom: 1px solid #ebedef;

    &__item {
        width: 16.5%;
        z-index: 1;
        transition: all .3s;

        @media screen and (max-width: 1019px) {
            width: 25%;
        }

        @media screen and (max-width: 767px) {
            width: 100%;
            height: 100%;
        }

        &:first-child {
            .accordion__item-wrap {
                padding-left: 0 !important;
            }
        }

        &:hover {
            cursor: pointer;

            .accordion__item-description {
                span {
                    opacity: 0;
                }

                .accordion__item-arrow {
                    opacity: 1;
                    
                }
            }
        }

        &_active {
            width: calc(100% - (16.5% * 2));
            @media screen and (max-width: 1019px) {
                width: calc(100% - (25% * 2));
                ;
            }

            @media screen and (max-width: 767px) {
                width: 100%;
            }
            .accordion__item-title {
                color: #ffffff;
                font-size: 32px;
                font-weight: 700;
                letter-spacing: 0.5px;
                line-height: 40px;
                max-width: 320px;

                @media screen and (max-width: 1019px) {
                    font-size: 30px;
                }
                @media screen and (max-width: 767px) {
                    font-size: 22px;
                    line-height: normal;
                    max-width: 250px;
                }
                
            }

            .accordion__item-description {
                span {
                    opacity: 1 !important;
                }

                .accordion__item-arrow {
                    opacity: 0 !important;
                    transition: all .15s;
                    @media screen and (max-width: 767px) {
                        opacity: 1 !important;
                        
                        position: absolute;
                        right: -4px;
                        top: 50%;
                        transform: translateY(-50%);
                        height: 100%;
                        display: flex;
                        align-items: center;
                    }
                }
            }

            .accordion__item-wrap {
                padding-left: 80px;
            }
        }

        &:nth-child(2) {
            border-left: 1px solid #f4f4f4;
            border-right: 1px solid #f4f4f4;
        }

        &-wrap {
            padding: 78px 30px 50px 23px;
            height: 100%;
            z-index: 1;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            box-sizing: border-box;
            transition: all .3s;
            @media screen and (max-width: 767px) {
                height: 100%;
                padding: 40px 0
            }
        }

        &-arrow {
            opacity: 0;
            transition: all .3s;
            bottom: 10px;
        }

        &-title {
            color: #ffffff;
            font-size: 20px;
            line-height: 28px;
            transition: all .3s;
            max-width: 170px;

            @media screen and (max-width: 1272px) {
                font-size: 18px;
            }
           
        }

        &-description {

            color: #ffffff;
            font-size: 15px;
            line-height: 24px;
            max-width: 214px;
            span {
                opacity: 0;
                transition: all .3s;

            }
            @media screen and (max-width: 767px) {
                margin-bottom: 20px;
            }
        }
    }

    &_theme {
        &_image {
            height: 650px;
            background: #ccc;
            display: flex;
            position: relative;
            transition: all .5s;
            background-repeat: no-repeat;
            background-size: cover;
            @media screen and (max-width: 767px) {
                height: 510px;
            }
            .container {
                display: flex;
                height: 100%;
                width: 100%;
                .owl-dots{
                    display: block;
                    position: absolute;
                    width: 100%;
                    bottom: 15px;
                    left: 0;
                    right: 0;
                    display: flex;
                    justify-content: center;
                    .owl-dot {
                        width: 10px;
                        height: 10px;
                        margin: 0 4px;
                        border: 1px solid #ffffff;
                        border-radius: 50px;
                        &.active{
                            background-color: #f4f4f4;
                        }
                    }
                }
                .owl-stage{
                    height: 100%;
                    .owl-item {
                        height: 100%;
                    }
                }

                @media screen and (max-width: 767px) {
                    width: 100%;
                    flex-wrap: wrap;
                }
            }

            &.disabled {
                &:after {
                    content: '';
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 0;
                    bottom: 0;
                    background: transparent;
                    z-index: 100;
                }
            }

        }

        &_сomments {
            &:first-child {
                margin-top: 55px;

                @media screen and (max-width: 767px) {
                    margin-top: 22px;
                }
            }

            .accordion__content {
                ul {
                    padding: 0;
                    margin: 0 0 0 0px;
                    list-style-type: none;
                    text-align: left;
                    padding-left: 40px;

                    li {
                        line-height: 20px;
                        font-size: 14px;
                        color: #323232;
                        margin-bottom: 10px;
                        position: relative;

                        &:after {
                            content: '—';
                            position: absolute;
                            color: #333;
                            font-size: 15px;
                            line-height: 24px;
                            left: -33px;
                            top: 0;
                        }
                    }
                }

                p {
                    text-indent: 0;
                    margin: 10px 0;
                    color: #333;
                    font-size: 15px;
                    line-height: 24px;
                }
            }
        }
    }

    &__item {
        color: #333;

        &-collapsed {
            .accordion__head {
                background-color: #ebedef;

                &:after {
                    transform: translateY(-50%) rotate(-180deg)
                }
            }
        }
    }

    &__title {
        @media screen and (max-width: 767px) {
            white-space: normal;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
        }
    }

    &__head {
        color: #333333;
        font-size: 15px;
        line-height: 24px;
        padding: 20px 20px 26px 26px;
        cursor: pointer;
        position: relative;

        @media screen and (max-width: 767px) {
            padding: 19px 40px 19px 15px;

        }


        &:hover {
            background-color: #ebedef;
        }

        &:after {
            content: '';
            position: absolute;
            right: 28px;
            top: 50%;
            transform: translateY(-50%);
            line-height: 57px;
            width: 32px;
            height: 32px;
            background-image: url(../img/arrow.png);
            transition: all .3s;

            @media screen and (max-width: 767px) {
                right: 10px;
            }
        }
    }

    &__content {
        padding: 5px 15px;
        padding-bottom: 30px;
        display: none;
    }
}

//text
.text {
    a {
        text-decoration: none;
        color: #34aaf2;
    }
}

//card-list
.card-list {
    background: #fff;
    position: relative;
    display: flex;
    flex-wrap: wrap;

    &_size {
        &_2 {
            .card-list__item {
                width: 50%;
                @media screen and (max-width: 767px) {
                    width: 100%;
                }
            }
        }

        &_4 {
            .card-list__item {
                width: 25%;

                @media screen and (max-width: 1019px) {
                    width: 100%;
                }

            }
        }
    }

    &__item {
        padding: 0;
        box-sizing: border-box;
        vertical-align: top;
        display: inline-block;
        width: 25%;

        &:nth-child(2n) {
            .card {
                border-left: 0;
            }
        }

        &:first-child {
            .tarif {
                border-left: 1px solid #ebedef;
            }
        }
    }
}

//card
.card {
    border: 1px solid #ebedef;
    height: 100%;
    background: #fff;
    &__title {
        color: #333333;
        font-size: 15px;
        line-height: 20px;
        padding: 0 20px;
        margin-bottom: 16px;

        white-space: normal;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;

        @media screen and (max-width: 1019px) {
            padding: 0 10px;
            font-size: 13px;
        }
        @media screen and (max-width: 640px) {
            height: 40px;
        }

        &:hover {
            text-decoration: underline;
        }
    }

    &__wrap {
        position: relative;

        @media screen and (max-width: 1019px) {
            height: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            flex-direction: column;
        }

    }

    &__photo {
        &-wrap {
            margin-bottom: 20px;
            position: relative;
            text-align: center;
            width: 100%;
            z-index: 1;
            padding: 0 20px;
            box-sizing: border-box;
            padding-top: 35px;
            display: flex;
            align-items: center;
            justify-content: center;

            @media screen and (max-width: 1019px) {
                padding: 0 10px;
                padding-top: 25px;

            }
            @media screen and (max-width: 640px) {
                height: 190px;
            }
        }

        a {
            display: block;

        }

        img {
            width: 100%;
            max-width: 82px;

            @media screen and (max-width: 1019px) {
                max-width: 55px;
            }
            @media screen and (max-width: 767px) {
                max-width: 100%
            }
        }
    }
}

//label 
.label {
    width: 56px;
    height: 56px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50px;
    color: #ffffff;
    font-size: 12px;
    font-weight: 400;
    letter-spacing: -0.6px;
    line-height: 15px;
    z-index: 2;

    @media screen and (max-width: 1019px) {
        width: 44px;
        height: 45px;
        font-size: 10px;

    }

    &_theme {
        &_green {
            background-color: #00b956;
        }
    }

    &_type {
        &_sale {
            position: absolute;
            right: 55px;
            top: 26px;

            @media screen and (max-width: 1019px) {
                right: 10px;
                top: 10px;
            }
        }
    }

}

//tabs
.tabs {
    margin-top: 40px;

    @media screen and (max-width: 767px) {
        margin-top: 32px;
    }

    &__content {
        display: none;
        margin-top: 32px;
    }

    &__head {
        min-height: 35px;
    }

    &__item {
        text-decoration: none;
        color: #000000;
        margin-right: 45px;
        display: inline-block;
        position: relative;

        @media screen and (max-width: 767px) {
            margin-right: 26px;
        }

        &:hover {
            color: #00b956;
        }

        &_active {
            color: #00b956;

            &:after {
                content: '';
                width: 100%;
                position: absolute;
                bottom: -10px;
                height: 2px;
                background-color: #00b956;
            }
        }

        &:last-child {
            margin-right: 0;
        }

        span {
            font-size: 15px;
            line-height: 24px;
            font-weight: 700;
        }
    }
}

//tarif
.tarif {
    border-top: 1px solid #ebedef;
    border-bottom: 1px solid #ebedef;
    border-right: 1px solid #ebedef;
    background-color: #ffffff;
    height: 100%;

    &__wrap {
        padding: 23px 20px 31px 20px;
        height: 100%;
        box-sizing: border-box;
    }

    &__link {
        text-align: center;
        width: 200px;

        .link_arrow_true {
            margin-left: -15px;
        }
    }

    &__title {
        color: #333333;
        font-size: 20px;
        font-weight: 700;
        letter-spacing: 0.5px;
        line-height: 28px;
        height: 56px;
        overflow: hidden;
    }

    &__time {
        color: #333333;
        font-size: 15px;
        font-weight: 400;
        line-height: 20px;
        margin-top: 15px;
    }

    &__offer {
        &-content {
            margin-top: 15px;
            min-height: 38px;

            @media screen and (max-width: 1272px) {
                min-height: 53px;
            }

            .icon {
                width: 42px;
                height: 20px;
                margin-top: 5px;
            }
        }

        &-text {
            color: #333333;
            font-size: 12px;
            font-weight: 400;
            line-height: 16px;
            margin-top: 38px;
            min-height: 32px;
            max-width: 75%;

            @media screen and (max-width: 1272px) {
                max-width: 100%;
            }
        }
    }

    &__button {
        margin-top: 26px;
    }

    &__price {
        margin-top: 50px;

        &-actual {
            color: #333333;
            font-size: 32px;
            font-weight: 600;
            letter-spacing: 0.5px;
            line-height: 40px;
        }

        &-period {
            color: #333333;
            font-size: 15px;
            line-height: 24px;
        }
    }

    &__button {
        width: 200px;
        height: 48px;
        border-radius: 100px;
        background-color: #00b956;
    }

    &__link {
        text-align: center;
        margin-top: 16px;

    }
}

//socials
.socials {
    .still {
        display: inline-block;
        color: #333333;
        font-size: 12px;
        line-height: 16px;

        &:hover {
            text-decoration: underline;
        }
    }
}

//social
.social {
    width: 35px;
    height: 35px;
    display: inline-block;
    margin-right: 6px;
    vertical-align: middle;

    &.instagram {
        background: url(../img/css_sprites.png) -66px -10px;
    }

    &.vk {
        background: url(../img/css_sprites.png) -10px -65px;
    }

    &.vimeo {
        background: url(../img/css_sprites.png) -65px -65px;
    }

    &.youtube {
        background: url(../img/css_sprites.png) -10px -10px
    }
}

//owl
.owl-dots {
    display: none;
}

@media screen and (max-width: 1019px) {
    .sliderTrif .owl-stage-outer {
        overflow: visible !important;
    }

    .sliderTrif .owl-stage {
        left: -50px;
    }

    .sliderTrif {
        position: relative;
        background: transparent;

    }

    .owl-carousel .owl-item {
        height: 100%;
    }
}

@media screen and (max-width: 767px) {
    .sliderTrif .owl-stage {
        left: 0;
    }
}

.noPadding {
    padding: 0 !important;
}

.sliderCard .owl-stage-outer{
    overflow: visible;
}